/* 초기화 코드 */
@font-face {
  font-family: KakaoSmall;
  font-weight: 300;
  src: url("./fonts/KakaoSmall-Light-v1.0.ef99f33.woff2") format("woff2"),
    url("./fonts/KakaoSmall-Light-v1.0.1e071e3.woff") format("woff");
}
@font-face {
  font-family: KakaoSmall;
  font-weight: 400;
  src: url("./fonts/KakaoSmall-Regular-v1.0.5c6e996.woff2") format("woff2"),
    url("./fonts/KakaoSmall-Regular-v1.0.0bbac94.woff") format("woff");
}
@font-face {
  font-family: KakaoSmall;
  font-weight: 700;
  src: url("./fonts/KakaoSmall_Bold_OTF.b852aad.woff2") format("woff2"),
    url("./fonts/KakaoSmall_Bold_OTF.ff0833f.woff") format("woff");
}
@font-face {
  font-family: KakaoBig;
  font-weight: 400;
  src: url("./fonts/KakaoBig-Regular-v1.0.4bf7d77.woff2") format("woff2"),
    url("./fonts/KakaoBig-Regular-v1.0.0e86a57.woff") format("woff");
}
@font-face {
  font-family: KakaoBig;
  font-weight: 700;
  src: url("./fonts/KakaoBig-Bold-v1.0.335a95e.woff2") format("woff2"),
    url("./fonts/KakaoBig-Bold-v1.0.79780f8.woff") format("woff");
}
@font-face {
  font-family: KakaoBig;
  font-weight: 800;
  src: url("./fonts/KakaoBig_ExtraBold_OTF.2612e28.woff2") format("woff2"),
    url("./fonts/KakaoBig_ExtraBold_OTF.b330e0d.woff") format("woff");
}

body {
  overflow-x: hidden;
  overflow-y: scroll;
  word-break: keep-all;
  word-wrap: break-word;
  background-color: var(--baseBackground);
  -ms-text-size-adjust: none;
  -moz-text-size-adjust: none;
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
}

body,
button,
input,
select,
td,
textarea,
th {
  font-size: 14px;
  line-height: 1.5;
  font-family: KakaoSmall, Apple SD Gothic Neo, Malgun Gothic, 맑은 고딕,
    sans-serif;
  font-weight: 400;
  color: #333;
}

blockquote,
body,
button,
code,
dd,
div,
dl,
dt,
fieldset,
form,
h1,
h2,
h3,
h4,
h5,
h6,
input,
legend,
li,
ol,
p,
pre,
select,
td,
textarea,
th,
ul {
  margin: 0;
  padding: 0;
}

dl,
li,
menu,
ol,
ul {
  list-style: none;
}

fieldset,
img {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
var {
  font-style: normal;
  font-weight: 400;
}

svg {
  vertical-align: top;
}

video {
  object-fit: contain;
  overflow-clip-margin: content-box;
  overflow: clip;
}

button {
  border: 0;
  background-color: transparent;
  cursor: pointer;
}

button,
input,
select,
textarea {
  vertical-align: middle;
  font-size: 100%;
}

a {
  color: #333;
}

a,
a:active,
a:hover {
  text-decoration: none;
}

.screen_out {
  position: absolute;
  width: 0px;
  height: 0px;
  overflow: hidden;
  line-height: 0;
  text-indent: -9999px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* 메인 페이지 */
/* header */
.doc-header {
  position: fixed;
  width: 100%;
  z-index: 2000;
  top: 0px;
  left: 0px;
  right: 0px;
  border: 0px;
  background-color: var(--baseBackground);
  transition: top 0.2s ease-in-out;
}
.head_type1.hide_header {
  top: -73px;
}
.head_type1.doc-header .wrap_header {
  height: 72px;
}
.wrap_header {
  height: 72px;
}
.doc-header .inner_gnb,
.doc-header .wrap_header {
  max-width: 1296px;
  margin: 0 auto;
}
.head_type1.doc-header .doc-title {
  padding-top: 18px;
}
.doc-header .doc-title {
  float: left;
}
.doc-title a {
  display: block;
  width: 74px;
  height: 24px;
}
.doc-title a .ico_logo {
