}
.head_type1 .doc-gnb {
  text-align: center;
}
.doc-gnb {
  text-align: center;
}
.list_gnb {
  font-family: KakaoBig, Apple SD Gothic Neo, Malgun Gothic, 맑은 고딕,
    sans-serif;
  display: inline-block;
  margin-left: -74px;
  font-size: 16px;
  letter-spacing: -0.5px;
}
.list_gnb:hover > li > a {
  cursor: pointer;
  color: #888888;
}
.head_type1 .doc-gnb .list_sub li a:hover {
