}
.list_gnb > li {
  display: inline-block;
  position: relative;
}
.list_gnb > li > a {
  display: block;
  padding: 0px 28px;
  line-height: 72px;
  font-weight: 700;
  color: var(--baseForeground);
}
.list_gnb > li > a:hover {
  color: var(--baseForeground);
}
.head_type1 .doc-gnb .list_gnb > li:first-child .list_sub {
  width: 267px;
}
.head_type1 .doc-gnb .list_sub {
  display: none;
  position: absolute;
  top: 60px;
  left: 4px;
  padding: 0 24.5px;
