}
.head_type1 .doc-gnb .list_sub li a {
  display: block;
  padding: 11px 0 13px;
  line-height: 24px;
  color: var(--colorBg1);
}
.head_type1 .doc-gnb .list_gnb > li.on .list_sub {
  display: block;
}
.head_type1 .doc-gnb .list_gnb > li.on > a {
  color: var(--baseForeground);
}

